$blackColor: rgba(71, 14, 14, 0.973);
$titleSize: 25px;
$color: rgb(241, 229, 229);
$fontFamilyNav: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;

@mixin genericButtonStyles($backgroundColor, $fontSize) {
    width: 200px;
    height: 40px;
    background-color: $backgroundColor;
    border: none;
    border-radius: 8px;
    color: $color;
    font-size: $fontSize;
}
